<template>
  <div class="container-fluid">
    <div class="row">
      <sidebar-admin></sidebar-admin>

      <main role="main" class="col-md-9 ml-sm-auto col-lg-10 px-4">
        <div
          class="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center pt-3 pb-2 mb-3"
        >
          <h1 class="h2"></h1>
          <button @click="openModal(true, null, 'reserve')" type="button" class="btn btn-outline-primary"></button>
        </div>
        <div class="table-responsive">
          <table class="table mt-4">
            <thead>
              <tr>
                <th width="120"></th>
                <th></th>
                <th width="120">%</th>
                <th width="120"></th>
                <th width="100"></th>
                <th width="130"></th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item) in coupons" :key="item.id">
                <td>{{ item.title }}</td>
                <td>{{ item.code }}</td>
                <td class="text-right">{{ item.percent}}</td>
                <td class="text-right">{{ item.due_date}}</td>
                <td>
                  <span v-if="item.is_enabled" class="text-success"></span>
                  <span v-else></span>
                </td>
                <td>
                  <button @click="openModal(false, item, 'reserve')" class="btn btn-outline-primary btn-sm mr-2"></button>
                  <button @click="openModal(null, item, 'delete')" class="btn btn-outline-dark btn-sm"></button>
                </td>
              </tr>
            </tbody>
          </table>

          <Pagination v-on:changePage-getPagination="getAdminCoupons"></Pagination>
        </div>
      </main>
    </div>

    <!-- modal -->
    <div
      class="modal fade"
      id="couponModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true">
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-12">
                <div class="form-group">
                  <label for="title"></label>
                  <input v-model="tempCoupon.title" type="text" class="form-control" id="title" placeholder="">
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="code"></label>
                    <input v-model="tempCoupon.code" type="text" class="form-control" id="code" placeholder="">
                  </div>
                  <div class="form-group col-md-6">
                    <label for="percent">%</label>
                    <input v-model="tempCoupon.percent" type="percent" class="form-control" id="percent" placeholder="%">
                  </div>
                </div>

                <div class="form-group">
                    <label for="due_date" class="col-form-label"></label>
                    <input v-model="tempCoupon.due_date" class="form-control" type="date" value="" id="due_date">
                </div>

                <div class="form-group">
                  <div class="form-check">
                    <input v-model="tempCoupon.is_enabled" class="form-check-input" type="checkbox" id="is_enabled">
                    <label class="form-check-label" for="is_enabled"></label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button @click="addCoupon(tempCoupon.id)" type="button" class="btn btn-primary"></button>
          </div>
        </div>
      </div>
    </div>

    <!-- modal -->
    <div
      class="modal fade"
      id="delCouponModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            
            <strong class="text-danger">{{ tempCoupon.title }}</strong> ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button @click="deleteCoupon(tempCoupon.id)" type="button" class="btn btn-danger"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
import moment from 'moment'
import 'bootstrap/dist/js/bootstrap.bundle.js'
import SidebarAdmin from '@/components/admin/Sidebar.vue'
import Pagination from '@/components/Pagination.vue'
import { mapActions } from 'vuex'

export default {
  name: 'couponAdmin',
  data () {
    return {
      tempCoupon: {},
      isNew: false
    }
  },
  computed: {
    coupons () {
      return this.$store.state.storeAdmin.coupons
    }
  },
  methods: {
    ...mapActions({
      getAdminCoupons: 'storeAdmin/getAdminCoupons'
    }),
    addCoupon (id) {
      const vm = this
      let api = `${process.env.VUE_APP_API_BASE_URL}/api/${process.env.VUE_APP_CUSTOM_PATH}/admin/coupon`
      let methods = 'post'
      let data = {
        'title': vm.tempCoupon.title,
        'is_enabled': vm.tempCoupon.is_enabled,
        'percent': vm.tempCoupon.percent,
        'due_date': moment(vm.tempCoupon.due_date).format('x'),
        'code': vm.tempCoupon.code
      }
      if (!vm.isNew) {
        api = `${process.env.VUE_APP_API_BASE_URL}/api/${process.env.VUE_APP_CUSTOM_PATH}/admin/coupon/${id}`
        methods = 'put'
        data = {
          'title': vm.tempCoupon.title,
          'is_enabled': vm.tempCoupon.is_enabled,
          'percent': vm.tempCoupon.percent,
          'due_date': moment(vm.tempCoupon.due_date).format('x'),
          'code': vm.tempCoupon.code
        }
      }
      this.$http[methods](api, { 'data': data }).then((res) => {
        console.log('[/]', res.data)
        if (res.data.success) {
          vm.getAdminCoupons({ page: vm.pagination.current_page })
          vm.closeModal('reserve')
        }
      })
    },
    openModal (bool, coupon, style) {
      if (style === 'reserve') {
        if (bool) {
          this.tempCoupon = {}
          this.isNew = true
        } else {
          this.tempCoupon = Object.assign({}, coupon)
          this.isNew = false
        }
        $('#couponModal').modal('show')
      } else if (style === 'delete') {
        this.tempCoupon = Object.assign({}, coupon)
        $('#delCouponModal').modal('show')
      }
    },
    closeModal (style) {
      if (style === 'reserve') {
        $('#couponModal').modal('hide')
      } else if (style === 'delete') {
        $('#delCouponModal').modal('hide')
      }
    },
    deleteCoupon (id) {
      const vm = this
      const api = `${process.env.VUE_APP_API_BASE_URL}/api/${process.env.VUE_APP_CUSTOM_PATH}/admin/coupon/${id}`
      this.$http.delete(api).then((res) => {
        console.log('[]', res.data)
        if (res.data.success) {
          vm.getAdminCoupons({ page: vm.pagination.current_page })
          vm.closeModal('delete')
        }
      })
    }
  },
  components: {
    SidebarAdmin,
    Pagination
  },
  created () {
    this.getAdminCoupons({}) // {page:1}
  }
}
</script>

