<template>
  <div>
    <div class="container-fluid">
      <div class="row">
        <sidebar-admin></sidebar-admin>

        <main role="main" class="ml-sm-auto col-10">
          <div
            class="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center pt-3 pb-2 mb-3 px-4"
          >
            <h1 class="h2"></h1>
            <button @click="openModal(true, undefined ,'reserve')" type="button" class="btn btn-outline-primary"></button>
          </div>
          <div class="table-responsive">
            <table class="table mt-4">
              <thead>
                <tr>
                  <th width="120"></th>
                  <th></th>
                  <th width="120"></th>
                  <th width="120"></th>
                  <th width="100"></th>
                  <th width="130"></th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item) in products" :key="item.id">
                  <td>{{ item.category }}</td>
                  <td>{{ item.title }}</td>
                  <td class="text-right">{{ item.origin_price | currency}}</td>
                  <td class="text-right">{{ item.price | currency}}</td>
                  <td>
                    <span v-if="item.is_enabled" class="text-success"></span>
                    <span v-else></span>
                  </td>
                  <td>
                    <button @click="openModal(false, item, 'reserve')" class="btn btn-outline-primary btn-sm mr-2"></button>
                    <button @click="openModal(undefined, item, 'delete')" class="btn btn-outline-dark btn-sm"></button>
                  </td>
                </tr>
              </tbody>
            </table>
            <Pagination v-on:changePage-getPagination="getAdminProduct"></Pagination>
          </div>
        </main>
      </div>
    </div>

    <!-- modal -->
    <div
      class="modal fade"
      id="productModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true">
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image"></label>
                  <input v-model="tempProduct.imageUrl" type="text" class="form-control" id="image" placeholder="">
                </div>
                <div class="form-group">
                  <label for="customFile">
                     
                    <i v-if="isLoadingShow" class="fas fa-spinner fa-spin"></i>
                  </label>
                  <input type="file" id="customFile" class="form-control"
                    ref="files"
                    @change="uploadFile">
                </div>
                <img
                  :src="tempProduct.imageUrl"
                  class="img-fluid"
                  alt
                >
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input v-model="tempProduct.title" type="text" class="form-control" id="title" placeholder="">
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category"></label>
                    <input v-model="tempProduct.category" type="text" class="form-control" id="category" placeholder="">
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input v-model="tempProduct.unit" type="unit" class="form-control" id="unit" placeholder="">
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="origin_price"></label>
                    <input v-model="tempProduct.origin_price" type="number" class="form-control" id="origin_price" placeholder="">
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input v-model="tempProduct.price" type="number" class="form-control" id="price" placeholder="">
                  </div>
                </div>
                <hr>

                <div class="form-group">
                  <label for="description"></label>
                  <textarea v-model="tempProduct.description" type="text" class="form-control" id="description" placeholder=""></textarea>
                </div>
                <div class="form-group">
                  <label for="content"></label>
                  <textarea v-model="tempProduct.content" type="text" class="form-control" id="content" placeholder=""></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input v-model="tempProduct.is_enabled" class="form-check-input" type="checkbox" id="is_enabled">
                    <label class="form-check-label" for="is_enabled"></label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button @click="addAdminProductThenClose({ isNew: isNew, data: tempProduct, id: tempProduct.id })" type="button" class="btn btn-primary"></button>
          </div>
        </div>
      </div>
    </div>

    <!-- modal -->
    <div
      class="modal fade"
      id="delProductModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            
            <strong class="text-danger">{{ tempProduct.title }}</strong> ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button @click="deleteProduct(tempProduct.id)" type="button" class="btn btn-danger"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import $ from 'jquery'
import 'bootstrap/dist/js/bootstrap.bundle.js'
import SidebarAdmin from '@/components/admin/Sidebar.vue'
import Pagination from '@/components/Pagination.vue'

export default {
  name: 'lessonAdmin',
  data () {
    return {
      tempProduct: {},
      isLoadingShow: false,
      isNew: true
    }
  },
  components: {
    SidebarAdmin,
    Pagination
  },
  computed: {
    products () {
      return this.$store.state.storeAdmin.products
    },
    pagination () {
      return this.$store.state.storeBasic.pagination
    }
  },
  methods: {
    ...mapActions({
      getAdminProduct: 'storeAdmin/getAdminProduct',
      addAdminProduct: 'storeAdmin/addAdminProduct',
      updateMessage: 'updateMessage'
    }),
    openModal (boolForNew, item, style) {
      this.$refs.files.value = '' // 
      if (style === 'reserve') {
        if (boolForNew) {
          this.tempProduct = {}
          this.isNew = true
        } else {
          this.tempProduct = Object.assign({}, item)
          this.isNew = false
        }
        $('#productModal').modal('show')
      } else if (style === 'delete') {
        this.tempProduct = Object.assign({}, item)
        $('#delProductModal').modal('show')
      }
    },
    closeModal (style) {
      if (style === 'reserve') {
        $('#productModal').modal('hide')
      } else if (style === 'delete') {
        $('#delProductModal').modal('hide')
      }
    },
    async addAdminProductThenClose ({ isNew, data, id }) {
      await this.addAdminProduct({ isNew, data, id })
      this.closeModal('reserve')
    },
    uploadFile () {
      const vm = this
      const api = `${process.env.VUE_APP_API_BASE_URL}/api/${process.env.VUE_APP_CUSTOM_PATH}/admin/upload`
      let formData = new FormData()
      let img = this.$refs.files.files[0]
      vm.isLoadingShow = true
      formData.append('file-to-upload', img)
      this.$http.post(api, formData, {
        headers: { 'Content-Type': 'multipart/form-data' }
      }).then((res) => {
        console.log('[]', res.data)
        vm.isLoadingShow = false
        if (res.data.success) {
          vm.$set(vm.tempProduct, 'imageUrl', res.data.imageUrl)
          vm.updateMessage({ message: '', status: 'success' })
          // vm.$bus.$emit('message:push', '', 'success')
        }
      })
    },
    deleteProduct (id) {
      const vm = this
      const api = `${process.env.VUE_APP_API_BASE_URL}/api/${process.env.VUE_APP_CUSTOM_PATH}/admin/product/${id}`
      this.$http.delete(api).then((res) => {
        if (res.data.success) {
          vm.getAdminProduct({ page: vm.pagination.current_page })
          vm.closeModal('delete')
        }
      })
    }
  },
  created () {
    this.getAdminProduct({ page: 1 })
  }
}
</script>

<style lang="scss" scoped>
</style>
